import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { Store } from '@ngxs/store';
import { UtilsService } from '../@core/utils.service';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Constants } from '../@global/constants';
import { FormCustomValidators } from '../@shared/validators/form-custom-validators';
import { Utils } from '../@global/utils';
import { IUserInfo } from '../@store/user/user.model';
import { UserState } from '../@store/user/user.state';
import { SetUserInfo, UploadProfileImage } from '../@store/user/user.actions';
import { take } from 'rxjs/operators';
import { InfoPopoverComponent } from '../@shared/components/info-popover/info-popover.component';
import { TranslateService } from '@ngx-translate/core';
import { PopoverController, Platform } from '@ionic/angular';
import { UpdateUserInput } from '../../API';
import { Subscription } from 'rxjs';
import { Camera } from '@ionic-native/camera/ngx';
import { InformService } from '../@core/inform.service';
import { ProfileImageEditorBasePage } from '../base/profile-image-editor-base.page';
import { GlobalInfoState } from '../@store/global-info/global-info.state';
import { IElement } from '../@store/global-info/global-info.model';
import { GetWorkAreaInfo } from '../@store/global-info/global-info.actions';

@Component({
  selector: 'app-edit-user-info',
  templateUrl: './edit-user-info.page.html',
  styleUrls: ['./edit-user-info.page.scss'],
})
export class EditUserInfoPage extends ProfileImageEditorBasePage implements OnInit, OnDestroy {
  public formGroup: FormGroup;
  public userInfo: IUserInfo;
  public hasUserLocation: boolean;
  public userLocation: string;
  private getUserInfoSubscription$: Subscription;
  public maxlength = Constants.MAX_LENGTH_PROFILE;
  public maxlengthName = Constants.MAX_LENGTH_NAME_COGNITO;
  public workAreaList: IElement[];
  private workAreaSubscription: Subscription;
  public customActionSheetWorkAreaOptions: any;
  public workAreaId: string;

  constructor(
    router: Router,
    location: Location,
    store: Store,
    utilsService: UtilsService,
    camera: Camera,
    informService: InformService,
    private formBuilder: FormBuilder,
    private popoverController: PopoverController,
    private translateService: TranslateService,
    private platform: Platform
  ) {
    super(location, router, store, utilsService, camera, informService);
  }

  ngOnInit() {
    this.subscribeToUserInfo();
    this.store.dispatch(new GetWorkAreaInfo());
    this.subscribeToWorkAreaInfo();
    this.initForm();

    const workAreaTitle = this.translateService.instant(`formFields.workArea`);
    this.customActionSheetWorkAreaOptions = {
      header: workAreaTitle,
    };
  }

  private initForm(): void {
    this.formGroup = this.formBuilder.group({
      name: [
        this.userInfo.name,
        [
          Validators.maxLength(this.maxlengthName),
          Validators.required,
          FormCustomValidators.notAllowedEmojis,
          FormCustomValidators.notAllowedNumbers,
        ],
      ],
      surname: [
        this.userInfo.surname,
        [
          Validators.maxLength(this.maxlengthName),
          Validators.required,
          FormCustomValidators.notAllowedEmojis,
          FormCustomValidators.notAllowedNumbers,
        ],
      ],
      occupation: [
        this.userInfo.occupation,
        [Validators.maxLength(this.maxlength), FormCustomValidators.notAllowedEmojis],
      ],
      nationality: [
        this.userInfo.nationality,
        [Validators.maxLength(this.maxlength), FormCustomValidators.notAllowedEmojis],
      ],
      birthdate: [
        this.userInfo.birthdate,
        [Validators.maxLength(this.maxlength), FormCustomValidators.notAllowedEmojis],
      ],
      work_area: [this.workAreaId, []],
      contactEmail: [
        this.userInfo.contact_email,
        [Validators.email, Validators.maxLength(Constants.MAX_LENGTH_USERNAME_COGNITO)],
      ],
      contactPhone: [this.userInfo.contact_phone, [Validators.maxLength(Constants.MAX_LENGTH_USERNAME_COGNITO)]],
      searching_education: [this.userInfo.searching_education],
      searching_work: [this.userInfo.searching_work],
    });
  }

  private subscribeToUserInfo(): void {
    this.getUserInfoSubscription$ = this.store.select(UserState.getUserInfo).subscribe((data) => {
      this.userInfo = Utils.copyObject(data) as IUserInfo;
      this.workAreaId = this.userInfo.work_area?.id || '';
      this.profileImage = this.userInfo?.photo;
      this.hasUserLocation = !!this.userInfo.country;
      if (this.hasUserLocation) {
        this.userLocation = Utils.generateUserFriendlyLocation(
          this.userInfo.country,
          this.userInfo.region,
          this.userInfo.city,
          this.userInfo.province,
          this.userInfo.region_name
        );
      }
    });
  }

  private subscribeToWorkAreaInfo(): void {
    this.workAreaSubscription = this.store.select(GlobalInfoState.getWorkArea).subscribe((data) => {
      this.workAreaList = data;
    });
  }

  public async saveProfile() {
    if (!this.utilsService.isValidForm(this.formGroup)) {
      return;
    }

    try {
      if (this.platform.is('cordova') && this.imageOriginalPath) {
        await this.uploadImageToBucket();
      }

      const currentUploadedPhotoKey = this.store.selectSnapshot(UserState.getcurrentUploadedPhotoKey);
      const data = (await this.utilsService.getUserDataToSend(
        this.formGroup,
        currentUploadedPhotoKey,
        this.formGroup.get('work_area').value
      )) as UpdateUserInput;
      this.store
        .dispatch(new SetUserInfo({ userInfo: data }))
        .pipe(take(1))
        .subscribe(
          () => {
            this.goBack();
          },
          (error: any) => {
            console.log(error);
          }
        );
    } catch (error) {
      console.error('[EditUserInfo] saveProfile');
      console.error(error);
    }
  }

  public getMinDate() {
    return Utils.getInitdate(100);
  }

  public getMaxDate() {
    return Utils.getInitdate(16);
  }

  public getDateDisplayFormat(): string {
    return Constants.DATE_DISPLAY_FORMAT;
  }

  public getDatePickerFormat(): string {
    return Constants.DATE_PICKER_FORMAT;
  }

  public async presentContactDataPopover($event) {
    await this.presentPopover($event, this.translateService.instant(`editProfile.contactInfoMessage`));
  }

  public async presentInterestsPopover($event) {
    await this.presentPopover($event, this.translateService.instant(`editProfile.interestsMessage`));
  }

  private async presentPopover($event, text) {
    const popover = await this.popoverController.create({
      event: $event,
      component: InfoPopoverComponent,
      cssClass: 'custom-alert',
      translucent: true,
      componentProps: { text },
    });
    return await popover.present();
  }

  public async selectImageFrom() {
    const buttons = [
      { text: 'cameraOptions.gallery', handler: this.pickImageGallery },
      { text: 'cameraOptions.camera', handler: this.pickImageCamera },
      { text: 'buttons.cancel' },
    ];

    await this.utilsService.showActionSheet('cameraOptions.actionSheetTitle', buttons);
  }

  private async uploadImageToBucket(): Promise<void> {
    const { path, filename } = Utils.getPathAndFilenameFromUrl(this.imageOriginalPath);
    const file = await this.utilsService.getFileFromSystemPath(path, filename);
    const filenameToUpload = Constants.PROFILE_FOLDER + '/' + Utils.generateFileUniqueName(file);

    if (!Utils.isMediaValidSize(file)) {
      this.informService.showWarning('createPost.invalidFileSize');
      throw new Error('invalidFileSize');
    }

    await this.store
      .dispatch(new UploadProfileImage({ filename: filenameToUpload, file, type: file.type }))
      .toPromise();
  }

  ngOnDestroy(): void {
    this.disconnectObservers();
  }

  private disconnectObservers(): void {
    if (this.getUserInfoSubscription$) {
      this.getUserInfoSubscription$.unsubscribe();
    }

    if (this.workAreaSubscription) {
      this.workAreaSubscription.unsubscribe();
    }
  }
}
